:root {
    --White: #FFFFFF;
    --Black: #111111;
    --Green: #32BB2F;
    --Blue: #00AAFF;
    --Orange: #FF6B00;
    --Gray: #8A8A8A;
}

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Arial', serif;
}

li {
    list-style: none;
}

a {
    text-decoration: none;
    color: var(--Black);
}

.container {
    margin: 0 auto;
    max-width: 80%;
}

.row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
    row-gap: 30px;
}

.col-2 {
    width: 50%;
    padding: 0 15px;
}

.col-3 {
    width: 33.3333%;
    padding: 0 15px;
}

.col-4 {
    width: 25%;
    padding: 0 15px;
}

.col-5 {
    width: 20%;
    padding: 0 15px;
}

.pt-100px {
    padding-top: 94px;
}

.poster-backgroundImage{
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    height: 530px;
}

@media screen and (max-width: 1024px) {
    body {
        overflow-x: hidden;
    }
    .container {
        max-width: 90%;
    }
    .pt-100px {
        padding-top: 55px;
    }

    .col-2 {
        width: 100%;
    }

    .col-3 {
        width: 50%;
    }

    .col-4 {
        width: 50%;
    }
}

@media screen and (max-width: 710px) {
    .col-3 {
        width: 100%;
    }
}