.blog__row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -50px;
    row-gap: 30px;
    justify-content: space-between;
}

.blog__row .col-3 {
    /*width: 33.3333%;*/
    padding: 0 50px;
}

.blog__box img {
    width: 100%;
}

.blog__box-item {
    position: relative;
    top: -40px;
    background: #FFFFFF;
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
    border-radius: 6px;
    padding: 16px 30px;
    display: flex;
    flex-direction: column;
    row-gap: 10px;
}

.blog__box-item a {
    text-align: right;
    color: var(--Blue);
    opacity: .7;
}

.blog__box-item a:hover {
    opacity: 1;
}

@media screen and (max-width: 1024px) {
    .blog__row {
        margin: 0 -15px;
    }

    .blog__row .col-3 {
        width: 100%;
        display: flex;
        padding: 0 30px;
        justify-content: center;
    }

    .blog__box {
        max-width: 300px;
    }
}

