.nav__list {
    display: flex;
    column-gap: 43px;
}
.nav__list li a {
    transition: .3s;
}

.nav__list li a:hover {
    color: var(--Green);
}

.active {
    color: var(--Green);
}

.nav__list li {
    position: relative;
}

.nav__list li a img {
    width: 10px;
    height: 10px;
    margin-left: 5px;
}

.nav__drop {
    position: absolute;
    top: 18px;
    left: 0;
    width: 240px;
    display: none;
    flex-direction: column;
    row-gap: 10px;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 0 18px rgba(0, 0, 0, 0.14);
    z-index: 11;
}

.nav__list li:hover .nav__drop {
    display: flex;
}


@media screen and (max-width: 1024px) {
    .nav__list {
        flex-direction: column;
        align-items: flex-end;
        row-gap: 24px;
        position: absolute;
        top: 0;
        right: 0;
        height: 100vh;
        width: 50%;
        padding-top: 64px;
        padding-right: 5%;
        background-color: #fff;
        transition: .3s;
    }

    .nav__list-hide {
        right: -100%;
    }
    .nav__drop {
        left: -170px;
    }
}