.about__box {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    row-gap: 20px;
    height: 100%;
}

.about__box p {
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 32px;
}

.about__box a {
    color: var(--Orange);
    font-size: 16px;
    opacity: .8;
}

.about__box a:hover {
    opacity: 1
}

.about__box img {
    width: 100%;
}

.about-wrap__box {
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.14);
    border-radius: 6px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: 20px;
    padding: 50px;
}

.about-wrap__box span:first-child {
    color: var(--Orange);
    font-size: 54px;
}


.sub-title2 {
    text-align: center;
    padding: 50px 0;
}

@media screen and (max-width: 1024px) {
    #about .about__row {
        flex-direction: row-reverse;
    }
}

@media screen and (max-width: 470px) {
    #about .col-4 {
        width: 100%;
    }
}