#feedback {
    padding-top: 70px;
}

.feedback__col-left {
    display: flex;
    flex-direction: column;
    justify-content: center;
    row-gap: 25px;
}

.feedback__col-left p:first-child {
    font-size: 40px;
    line-height: 60px;
    color: #0A142F;
}

.feedback__col-left p:last-child {
    font-size: 20px;
    line-height: 30px;
    color: #0A142F;
}