#about-poster .container {
    height: 100%;
}

.poster__row {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    row-gap: 23px;
    color: #fff;
}

.poster__row h2 {
    font-size: 40px;
}

.poster__row span {
    font-size: 26px;
}

@media screen and (max-width: 1024px) {
    .poster__row {
        text-align: center;
    }

    .poster__row h2 {
        font-size: 30px;
    }
}

