.about-brands__row {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.about-brands__row {
    width: 100%;
}


@media screen and (max-width: 1024px) {
    .about-brands__row img {
        width: 100px;
    }
}

@media screen and (max-width: 580px) {
    .about-brands__row img {
        width: 60px;
    }
}