#about-answers .col-3:nth-child(2) {
    width: 66.6666%;
}

.about-answers__row {
    margin-bottom: 70px;
}

.about-answers__title {
    text-align: center;
    font-size: 26px;
    padding: 40px 0;
}


.about-answers__box {
    box-shadow: 0 0 18px rgba(0, 0, 0, 0.14);
    border-left: 5px solid var(--Green);
    padding: 30px;
    height: 100%;
}

@media screen and (max-width: 1024px) {
    #about-answers .col-3:nth-child(2) {
        width: 50%;
    }
}

@media screen and (max-width: 710px) {
    #about-answers .col-3:nth-child(2) {
        width: 100%;
    }
}
