.swiper-wrapper {
    margin-bottom: 30px;
}

.swiper-pagination-bullet-active {
    background: var(--Green)!important;
}

.slider-item, .slider-item-second {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    height: 530px;
}

.slider-item__box {
    width: 50%;
    background-color: rgba(1, 41, 68, 0.28);

    color: var(--White);
    font-size: 21px;
    line-height: 31px;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    height: 100%;

    padding: 0 30px 0 10%;
}

.slider-item__box p {
    margin-bottom: 32px;
}

.slider-item__box span {
    font-weight: 700;
}


.slider-item__box button, .slider__btn-second button {
    border: none;
    border-radius: 5px;
    background-color: var(--Green);
    color: var(--White);
    padding: 8px 30px;
    font-size: 21px;
    cursor: pointer;
    transition: .3s;

}

.slider-item__box button:hover, .slider__btn-second button:hover {
    background-color: #1F731E;
}

.slider-item-second {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: 56px;

    padding: 5%;
    color: var(--White);
    font-size: 21px;
    line-height: 31px;
    text-align: center;
}

.slider-item__box-second h2 {
    font-size: 26px;
    margin-bottom: 24px;
}



@media screen and (max-width: 1024px) {

    .slider-item__box {
        width: 100%;
        padding: 0 5%;
        background: none;
    }
}

@media screen and (max-width: 724px) {
    .slider-item {
        background-size: cover;
        background-position: -550px;

    }
}