.blog-details__container {
    display: flex;
    column-gap: 30px;
    padding-top: 130px;
}

.blog-details__left {
    width: 66.6666%;
}

.blog-details__date {
    display: flex;
    align-items: center;
    column-gap: 10px;
    padding-bottom: 40px;
}


.blog-details__left-post-box {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 30px 0;
}


.blog-details__date span {
    font-size: 12px;
    color: #5A5C61;
}

.blog-details__left-post p {
    line-height: 30px;
}


.blog-details__right {
    width: 33.3333%;
}

.blog-details__right-row .blog__box {
    padding: 0 30px;
}

.blog-details__right-row {
    padding-top: 55px;
}

@media screen and (max-width: 1300px) {
    .blog-details__right-row .blog__box {
        padding: 0;
    }
}


@media screen and (max-width: 1060px) {

    .blog-details__container {
        flex-wrap: wrap;
    }

    .blog-details__left {
        width: 100%;
    }


    .blog-details__right {
        width: 100%;
        margin-top: 20px;
    }

    .blog-details__right-row {
        display: flex;
        justify-content: center;
        column-gap: 30px;
        flex-wrap: wrap;
        padding-top: 0;
    }
    .blog-details__right-row .blog__box {
        width: 45%;
    }

}

@media screen and (max-width: 680px)  {
    .blog-details__left-post-box img {
        width: 100%;
    }

    .blog-details__right-row .blog__box {
        width: 100%;
    }

}