.services__box {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    border-radius: 21px;
    cursor: pointer;
}

.services__box:after {
    content: "";
    background: rgba(8, 52, 74, 0.6);
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

.services__box__img {
    width: 100%;
    transition: 0.3s;
}

.services__box:hover .services__box__img {
    transform: scale(1.2);
}

.services__box p {
    position: absolute;
    color: #fff;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
    padding: 0 55px;
    z-index: 1;
}

