.services__link-box {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    border-radius: 26px;
    cursor: pointer;
}

.services__link-box:after {
    content: "";
    background-color: #1F731E;
    position: absolute;
    opacity: 0;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    transition: .3s;
    z-index: 1;

}

.services__link-img {
    width: 100%;
}

.services__link-box:hover:after {
    opacity: 1;
}

.services__link-box span {
    position: absolute;
    color: #fff;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
    z-index: 3;
}


