.header-top {
    background: var(--Green);
    height: 37px;
    transition: .3s;
}

.header-top-hide {
    padding: 0;
    height: 0;
    transition: 1s;
}

.header-top__container {
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 100%;
}

.header-top__left {
    display: flex;
    column-gap: 50px;
}

.header-top__left__item {
    display: flex;
    align-items: center;
    column-gap: 5px;
}

.header-top__left__item p {
    color: var(--White);
    font-size: 12px;
}


/* header right */

.header-top__right {
    display: flex;
    align-items: center;
    column-gap: 15px;
}

@media screen and (max-width: 1024px) {
    .header-top {
        display: none;
    }
}