.pagination {
    display: flex;
    column-gap: 10px;
    justify-content: center;
}
.pagination-btn {
    border: 1px solid #F5F5F5;
    border-radius: 2px;
    padding: 5px 11px;
    cursor: pointer;
    display: flex;
    align-items: center;
}

.active-btn {
    border: 1px solid var(--Green);
    color: var(--Green);
    padding: 5px 11px;
}

.left-arrow img {
    rotate: 180deg;
}