.chat-nav {
    position: fixed;
    bottom: 20px;
    right: 20px;
    z-index: 7;
    display: flex;
    flex-direction: column;
    row-gap: 24px;
}

.chat-nav__item:first-child, .chat-nav__item:nth-child(2)  {
    position: absolute;
    top: 0;
    transition: .3s;
    opacity: 0;
}

.chat-nav__item:nth-child(2) {
    z-index: 7;
}

.chat-nav__item:first-child {
    z-index: 8;
}

.chat-nav__item:nth-child(3) {
    position: relative;
    z-index: 10;
    opacity: .7;
    transition: .3s;
}

.chat-nav__item:nth-child(3):hover {
    opacity: 1;
}

.isVisible {
    opacity: 1!important;

}

.isOpen {
    top: -100px!important;
    opacity: 1!important;
}

.isOpen:nth-child(2) {
    top: -200px!important;
}
