.services-feed__box {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    border-radius: 21px;
    cursor: pointer;
    height: 100%;
}

.services-feed__box:after {
    content: "";
    background: rgba(8, 52, 74, 0.6);
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

.services-feed__box__img {
    width: 100%;
    height: 100%;
    transition: 0.3s;
}

.services-feed__box:hover .services-feed__box__img {
    transform: scale(1.2);
}

.services-feed__box p {
    position: absolute;
    color: #fff;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
    padding: 0 55px;
    z-index: 1;
}


.services-feed__row .col-3:last-child, .services-feed__row .col-3:nth-child(7) {
    width: 50%;
}

@media screen and (max-width: 1024px) {
    .services-feed__row .col-3 {
        min-height: 274px;
    }
}


@media screen and (max-width: 710px) {
    .services-feed__row .col-3 {
        max-height: 205px;
    }

    .services-feed__row .col-3:last-child, .services-feed__row .col-3:nth-child(7) {
        width: 100%;
    }
}