#contacts {
    padding-top: 140px;
}

.contacts__container {
    display: flex;
    column-gap: 50px;
}

.contacts__left-row {
    display: flex;
    flex-direction: column;
    row-gap: 24px;
}

.contacts__left-col {
    display: flex;
    align-items: center;
    column-gap: 16px;
}

.contacts__left {
    width: 30%;
}

.contacts__right {
    width: 70%;
    height: 338px;
}

@media screen and (max-width: 880px) {
    .contacts__container {
        flex-wrap: wrap;
        row-gap: 40px;
    }

    .contacts__left {
        width: 100%;
    }

    .contacts__right {
        width: 100%;
    }
}

