header {
    background: #fff;
    position: fixed;
    width: 100%;
    z-index: 10;
    box-shadow: 0 11px 8px rgba(0, 0, 0, 0.1);
}

.header__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 0;
}

.header__nav a img {
    width: 43px;
}






.burger-menu {
    display: none;
}


.burger-btn {
    width: 24px;
    height: 20px;
    display: flex;
    align-items: center;
    cursor: pointer;
    position: relative;
}

.burger-btn span{
    width: 100%;
    height: 3px;
    border-radius: 10px;
    display: block;
    background-color: #111111;
    position: relative;
    transition: .5s;
}

.burger-btn span:before, .burger-btn span:after{
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    border-radius: 10px;
    background-color: #111111;
    transition: .5s;

}
.burger-btn span:before{
    top: -8px;
}

.burger-btn span:after{
    top: 8px;
}

.show-btn span {
    transition: .5s;
    transform: rotate(45deg);
}


.show-btn span:before, .show-btn span:after {
    transform: rotate(90deg);
    top: 0;
    bottom: 0;
    transition: .5s;
}

@media screen and (max-width: 1024px) {
    .burger-menu {
        display: block;
    }
}