#services-poster {
    position: relative;
}

#services-poster .container {
    height: 100%;
}

.services-poster__row {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    height: 100%;
    row-gap: 23px;
    color: #fff;
}

.services-poster__row h2 {
    font-size: 80px;
    text-align: center;
}

.services-poster__row span {
    font-size: 16px;
    text-align: center;
}

@media screen and (max-width: 510px) {
    .services-poster__row h2 {
        font-size: 30px;
    }
}