.swiper-button-next, .swiper-button-prev {
    color: #fff;
}

.location__box {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.location__box img {
    width: 100%;
}

.location__box p {
    position: absolute;
    font-weight: 700;
    font-size: 32px;
    color: var(--White);
}