.tasks__row {
    display: flex;
    justify-content: flex-end;
    padding: 40px 0;
}

.tasks__box {
    background-color: var(--Blue);
    border-radius: 9px;
    width: 55%;
    color: #FFFFFF;
    padding: 80px 70px;
}

.tasks__box p {
    font-size: 26px;
    text-align: center;
    margin-bottom: 40px;
}

.tasks__box ul {
    line-height: 28px;
}

.tasks__box ul li {
    list-style-type: disc;
    padding-right: 10px;
    font-size: 16px;
}

@media screen and (max-width: 1024px) {
    .tasks__box {
        width: 100%;
    }
}



