.feedback__form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    row-gap: 20px;
    box-shadow: 0 20px 105.701px rgba(51, 51, 51, 0.1);
    border-radius: 20px;
    padding: 50px 40px;
}

.feedback__form input {
    font-size: 18px;
    width: 100%;
    border: 1px solid #6C7282;
    outline: none;
    border-radius: 5px;
    padding: 20px 25px;
}

.feedback__form textarea {
    height: 200px;
    width: 100%;
    font-size: 18px;
    border: 1px solid #6C7282;
    border-radius: 5px;
    padding: 20px 25px;
    resize: none;
    outline: none;
}


.feedback__form input:focus, .feedback__form textarea:focus {
    border-color: var(--Green);
}

.feedback__form button {
    font-size: 14px;
    background-color: var(--Green);
    border: none;
    box-shadow: 0 24px 34px rgba(32, 70, 100, 0.1);
    border-radius: 10px;
    color: var(--White);
    padding: 17px 42px;
}
