.blog-feed__row {
    justify-content: space-between;
}

.blog-feed__row .col-3 {
    width: 30%;
}

@media screen and (max-width: 1024px) {
    .blog-feed__row .col-3 {
        width: 50%;
        display: flex;
        justify-content: center;
    }
}

@media screen and (max-width: 510px) {
    .blog-feed__row .col-3 {
        width: 100%;
    }
}