footer {
    background-color: #464F60;
    padding: 50px 0;
    margin-top: 100px;
}

footer p {
    color: #ffffff;
    font-size: 14px;
    line-height: 24px;
}

.footer__row {
    display: flex;
    justify-content: space-between;
    column-gap: 60px;
    row-gap: 40px;
}

.footer__nav-title {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 20px;
}

.footer__nav-items {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
}

.footer__nav-items p pre {
    font-family: 'Arial', serif;
}

.footer__nav-items a {
    color: #ffffff;
}


.footer__brand {
    width: 30%;
}



.footer__map img {
    width: 100%;
}

@media screen and (max-width: 1024px) {
    .footer__row {
        flex-wrap: wrap;
    }

    .footer__brand, .footer__nav, .footer__contacts, .footer__map {
        width: 45%;
    }

}

@media screen and (max-width: 645px) {
    .footer__brand, .footer__nav, .footer__contacts, .footer__map {
        width: 100%;
    }
}




















